.org-axis-container {
  font-family: Arial, sans-serif;
  padding: 1rem;
}

/* Legend styles */
.legend {
  margin-bottom: 1rem;
  border-bottom: 1px solid #eee;
  padding-bottom: 1rem;
}

.legend h3 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-size: 1rem;
  font-weight: normal;
}

.legend-items {
  display: flex;
  gap: 1.5rem;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.color-box {
  width: 16px;
  height: 16px;
  border: 1px solid #ccc;
}

/* Toolbar styles */
.toolbar {
  display: flex;
  justify-content: space-between;
  padding: 1rem 0;
  margin-bottom: 1rem;
}

.filter-section,
.search-section {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.toolbar select,
.toolbar input {
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.refresh-button {
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

.refresh-button:hover {
  background-color: #e0e0e0;
}

/* Table styles */
.org-table {
  width: 100%;
  border-collapse: collapse;
}

.org-table th {
  background-color: #8B0000; /* Dark red background */
  color: white;
  text-align: left;
  padding: 0.75rem;
  font-weight: bold;
}

.org-table tbody tr:nth-child(odd) {
  background-color: #f9f9f9;
}

.org-table tbody tr:hover {
  background-color: #f0f0f0;
}

.org-table td {
  padding: 0.75rem;
  border-bottom: 1px solid #ddd;
}

/* Expand/collapse button */
.expand-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 0.8rem;
  padding: 0 0.5rem 0 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

/* Error and loading styles */
.error-message {
  color: #d32f2f;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: #ffebee;
  border-radius: 4px;
}

.loading {
  text-align: center;
  padding: 2rem;
  color: #666;
}

/* Action buttons */
.org-table button {
  margin-right: 0.5rem;
  padding: 0.25rem 0.5rem;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 3px;
  cursor: pointer;
}

.org-table button:hover {
  background-color: #e0e0e0;
}

/* Node styles based on level */
.level-group {
  font-weight: bold;
  color: #0066CC;
}

.level-entity {
  color: #FFA500;
  margin-left: 20px;
}

.level-site {
  color: #00A651;
  margin-left: 40px;
} 