.navigation {
  width: 250px;
  background-color: #333;
  color: white;
  height: 100%;
  padding: 1rem 0;
}

.nav-header {
  padding: 0 1.5rem 1.5rem;
  border-bottom: 1px solid #444;
  margin-bottom: 1rem;
}

.nav-title {
  font-size: 1.5rem;
  margin: 0;
}

.nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-item {
  padding: 0.8rem 1.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: background-color 0.2s ease;
}

.nav-item:hover {
  background-color: #444;
}

.nav-item.active {
  background-color: #00A651;
  font-weight: bold;
}

.nav-icon {
  margin-right: 0.75rem;
  display: inline-block;
  width: 20px;
  text-align: center;
}

.nav-label {
  flex: 1;
} 